.slider-input{
  -webkit-appearance:none !important;
  width:100%;
  height:2px;
  background:var(--theme-color);
  border:none;
  outline:none;
  &::-webkit-slider-thumb {
    -webkit-appearance:none !important;
    width:20px;
    height:20px;
    background:#fcfcfc;
    border:2px solid var(--theme-color);
    border-radius:50%;
    cursor:pointer;
}
}