
:root{
  --label-color:#{rgb(0, 0, 0)};
  --label-color-focus:#{white};
  --input-active-color:#{rgb(207, 3, 3)};
  --input-background-color:#{rgb(255, 255, 255)};
  --theme-color-primary:#{#ff3c3c};
}

.App {
  display: flex;
  height: 100%;
  width: 100%;
  min-height: 100vh;
  min-width: 100vw;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(56, 56, 56);
}


.btn-wrapper{
  width: 80%;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translate(-50%, 0%);
  margin: 0;

}

.input-span{
  position: relative;
  display: inline-block;
  margin: 20px 10px;
}

.pop-up {
  display: inline-block;
  width: 100%;
  padding: 20px 10px 20px 15px;//top right bottom left
  // padding: 20px;
  font-family: "Open Sans", sans;
  font-weight: 400;
  color: var(--input-active-color);
  border-radius: 10px;
  box-shadow: 0 10px 25px rgba(0,0,0,.1);
  background-color: var(--input-background-color);
  border: 0;
  outline: 0;
  // text-indent: 60px; // Arbitrary.
  transition: all .3s ease-in-out;
  
  &::-webkit-input-placeholder {

    text-indent: 0;
    font-weight: 300;
  }

  + .lbl {
    display: inline-block;
    position: absolute;
    top: -30px;
    left: 0;
    // bottom: 8px;
    padding: 7px 10px;
    color: var(--label-color);
    font-size: 11px;
    font-weight: 700;
    text-transform: uppercase;
    text-shadow: 0 1px 0 rgba(19,74,70,0);
    transition: all .3s ease-in-out;
    border-radius: 3px;
    background: rgba(122,184,147,0);
    
    &:after {
      position: absolute;
      content: "";
      width: 0;
      height: 0;
      top: 100%;
      left: 50%;
      margin-left: -3px;
      border-left: 3px solid transparent;
      border-right: 3px solid transparent;
      border-top: 3px solid rgba(122,184,147,0);
      transition: all .3s ease-in-out;
    }
  }
}

.pop-up:focus,
.pop-up:active {
  // color: #000000;
  color: var(--input-active-color);

  text-indent: 0;
  &::-webkit-input-placeholder {
    color: #aaa;
  }
  + .lbl {
    color: var(--label-color-focus);
    border-radius: 5px;
    background: var(--theme-color-primary);
    transform: translateY(-10px) scale(1.1);
    &:after {
      border-top: 9px solid var(--theme-color-primary);
    }
  }
}

input{
  margin-bottom: 10px;
}

.error{
  margin-top: 10px;
  padding: 10px;
  color: red;
}


.btn {
  background: var(--theme-color-primary);
  // position: absolute;
  border-radius: 15px;
  padding: 20px 20px;
  color: #fff;
  text-decoration: none;
   font-size: 1.2rem; 
  // align-items: center;
  width: 100%;
  // margin: 20px 15px;
  border: none;
  // bottom: 10px;
}

.btn:hover { 
  -webkit-animation: hover 1200ms linear 2 alternate;
  animation: hover 1200ms linear 2 alternate;
}

.btn:active {
  -webkit-animation: active 1200ms ease 1 alternate;
  animation: active 1200ms ease 1 alternate; 
  background: #5F9BE0;
}





@-webkit-keyframes active { 
  0% {transform: scale(1,1);}
  90% {transform: scale(.9,.88);}
  100% {transform: scale(.92,.9);}
}

@keyframes active { 
  0% {transform: scale(1,1);}
  90% {transform: scale(.9,.88);}
  100% {transform: scale(.92,.9);}
}

